.App {
  text-align: center;
}

.video-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.video-item {
  width: 240px;
  margin: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
}

.video-item img {
  width: 100%;
  height: auto;
}

.video-item h4 {
  padding: 10px;
}

.category-filter {
  margin: 20px 0;
}

.category-filter button {
  padding: 10px 20px;
  margin: 0 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #f0f0f0;
}

input[type="text"] {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  width: calc(100% - 40px);
  max-width: 300px;
  margin: 20px 0;
}
